import $ from 'jquery'

$.prototype.contains = function (obj) {
  if (!obj) return false;

  if (Array.isArray(obj)) {
    for (var i = 0; i < obj.length; i++) {
      if (this.contains(obj[i]) && this != obj[i]) return true;
    }

    return false;
  }

  for (var i = 0; i < this.length; i++) {
    if (this[i].nodeType == Node.ELEMENT_NODE && this[i].contains(obj) && this[i] != obj) {
      return true;
    }
  }

  return false;
}


$.prototype.findVisible = function (selector) {
  var $els = this.find(selector);

  for (var i = $els.length - 1; i >= 0; i--) {
    if (!$($els[i]).isVisible()) {
      $els.splice(i, 1);
    }
  }

  return $els;
}

$.prototype.isVisible = function () {
  return this.css('display') != 'none' && this.css('visiblity') != 'hidden' && this.attr('type') != 'hidden';
}