import FE from 'froala_editor'

FE.ICON_TEMPLATES = {
  font_awesome: '<i class="fa fa-[NAME]" aria-hidden="true"></i>',
  font_awesome_5: '<i class="fas fa-[FA5NAME]" aria-hidden="true"></i>',
  font_awesome_5r: '<i class="far fa-[FA5NAME]" aria-hidden="true"></i>',
  font_awesome_5l: '<i class="fal fa-[FA5NAME]" aria-hidden="true"></i>',
  text: '<span style="text-align: center;">[NAME]</span>',
  image: '<img src=[SRC] alt=[ALT] />',
  svg: '<svg viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg">[PATH]</svg>'
}

FE.ICONS = {
  bold: {
    NAME: 'bold'
  },
  italic: {
    NAME: 'italic'
  },
  underline: {
    NAME: 'underline'
  },
  strikeThrough: {
    NAME: 'strikethrough'
  },
  subscript: {
    NAME: 'subscript'
  },
  superscript: {
    NAME: 'superscript'
  },
  color: {
    NAME: 'tint'
  },
  outdent: {
    NAME: 'outdent'
  },
  indent: {
    NAME: 'indent'
  },
  undo: {
    NAME: 'rotate-left',
    FA5NAME: 'undo'
  },
  redo: {
    NAME: 'rotate-right',
    FA5NAME: 'redo'
  },
  insertHR: {
    NAME: 'minus'
  },
  clearFormatting: {
    NAME: 'eraser'
  },
  selectAll: {
    NAME: 'mouse-pointer'
  }
}

FE.DefineIconTemplate = function (name, options) {
  FE.ICON_TEMPLATES[name] = options
}

FE.DefineIcon = function (name, options) {
  FE.ICONS[name] = options
}

Object.assign(FE.DEFAULTS, {
  iconsTemplate: 'font_awesome'
})

FE.MODULES.icon = function (editor) {
  function create(command) {
    let icon = null
    const info = FE.ICONS[command]

    if (typeof info !== 'undefined') {
      let template = info.template || FE.ICON_DEFAULT_TEMPLATE || editor.opts.iconsTemplate

      if (!info.FA5NAME) {
        info.FA5NAME = info.NAME
      }

      if (template && (template = FE.ICON_TEMPLATES[template])) {
        icon = template.replace(/\[([a-zA-Z0-9]*)\]/g, (str, a1) => {
          if (a1 === 'NAME') {
            return info[a1] || command
          }

          return info[a1]
        })
      }
    }

    return icon || command
  }

  function getTemplate(command) {
    const info = FE.ICONS[command]
    let template = editor.opts.iconsTemplate

    if (typeof info !== 'undefined') {
      template = info.template || FE.ICON_DEFAULT_TEMPLATE || editor.opts.iconsTemplate

      return template
    }

    return template
  }

  return {
    create,
    getTemplate
  }
}
